/* montserrat-regular - latin_cyrillic */
@font-face {
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 400;
  src: local(''),
  url('assets/fonts/montserrat/montserrat-v25-latin_cyrillic-regular.woff2') format('woff2'), /* Chrome 26+, Opera 23+, Firefox 39+ */
  url('assets/fonts/montserrat/montserrat-v25-latin_cyrillic-regular.woff') format('woff'); /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}
/* montserrat-500 - latin_cyrillic */
@font-face {
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 500;
  src: local(''),
  url('assets/fonts/montserrat/montserrat-v25-latin_cyrillic-500.woff2') format('woff2'), /* Chrome 26+, Opera 23+, Firefox 39+ */
  url('assets/fonts/montserrat/montserrat-v25-latin_cyrillic-500.woff') format('woff'); /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}
/* montserrat-700 - latin_cyrillic */
@font-face {
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 700;
  src: local(''),
  url('assets/fonts/montserrat/montserrat-v25-latin_cyrillic-700.woff2') format('woff2'), /* Chrome 26+, Opera 23+, Firefox 39+ */
  url('assets/fonts/montserrat/montserrat-v25-latin_cyrillic-700.woff') format('woff'); /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}
@font-face {
  font-family: raleway_c;
  src: url("assets/fonts/raleway/raleway-v22-cyrillic-100.woff2");
  font-weight: 100;
}
@font-face {
  font-family: raleway_c;
  src: url("assets/fonts/raleway/raleway-v22-cyrillic-300.woff2");
  font-weight: 300;
}

$back-color-dark: #1a1a1a;
$back-color-light: #2d2d2d;
$text-color-light: #f3f3f3;
$text-color-peachy: #faf3e6;
$text-color-dark: #3b3b3b;
$links-color: #eaa20c;
$text-color-wood: #672f02;
$text-color-lightblue: #55c2ff;
$border-color-dark: #232323;
$border-color-peach: #e1b667;
$border-color-orange: #9a4707;
$border-color-lightgrey: #6b6b6b;
$border-color-superlightgrey: #a2a2a2;
$border-color-blue: #00a3ff;
$hover-color-dark: #3d3d3d;
$hover-color-wood: #8a4500;
$danger-color: #fd6868;
$valid-color: #aaff8e;
$active-border-color: rgba(170, 255, 142, 1);
$header-color: #343434;
$data-color: #424242;


html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, tt, var,
center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* make sure to set some focus styles for accessibility */
:focus {
  outline: 0;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  font-family: 'Montserrat';
  font-style: normal;
  font-size: 16px;
  line-height: 1.2;
  font-weight: normal;
  color: $text-color-peachy;
  background-color: $back-color-dark;
  //background-image: url("assets/images/chalkboard.jpeg");
  //background-size: cover;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  -webkit-appearance: none;
  -moz-appearance: none;
}

input[type=search] {
  -webkit-appearance: none;
  -moz-appearance: none;
  -webkit-box-sizing: content-box;
  -moz-box-sizing: content-box;
  box-sizing: content-box;
}

textarea {
  overflow: auto;
  vertical-align: top;
  resize: vertical;
}

/**
 * Correct `inline-block` display not defined in IE 6/7/8/9 and Firefox 3.
 */

audio,
canvas,
video {
  display: inline-block;
  *display: inline;
  *zoom: 1;
  max-width: 100%;
}

/**
 * Prevent modern browsers from displaying `audio` without controls.
 * Remove excess height in iOS 5 devices.
 */

audio:not([controls]) {
  display: none;
  height: 0;
}

/**
 * Address styling not present in IE 7/8/9, Firefox 3, and Safari 4.
 * Known issue: no IE 6 support.
 */

[hidden] {
  display: none;
}

/**
 * 1. Correct text resizing oddly in IE 6/7 when body `font-size` is set using
 *    `em` units.
 * 2. Prevent iOS text size adjust after orientation change, without disabling
 *    user zoom.
 */

html {
  font-size: 16px; /* 1 */
  -webkit-text-size-adjust: 100%; /* 2 */
  -ms-text-size-adjust: 100%; /* 2 */
  box-sizing: border-box;
}

*, *::before, *::after{
  box-sizing: inherit;
}

/**
 * Address `outline` inconsistency between Chrome and other browsers.
 */

a:focus {
  outline: thin dotted;
}

/**
 * Improve readability when focused and also mouse hovered in all browsers.
 */

a:active,
a:hover {
  outline: 0;
}

/**
 * 1. Remove border when inside `a` element in IE 6/7/8/9 and Firefox 3.
 * 2. Improve image quality when scaled in IE 7.
 */

img {
  border: 0; /* 1 */
  -ms-interpolation-mode: bicubic; /* 2 */
}

/**
 * Address margin not present in IE 6/7/8/9, Safari 5, and Opera 11.
 */

figure {
  margin: 0;
}

/**
 * Correct margin displayed oddly in IE 6/7.
 */

form {
  margin: 0;
}

/**
 * Define consistent border, margin, and padding.
 */

fieldset {
  border: 1px solid #c0c0c0;
  margin: 0 2px;
  padding: 0.35em 0.625em 0.75em;
}

/**
 * 1. Correct color not being inherited in IE 6/7/8/9.
 * 2. Correct text not wrapping in Firefox 3.
 * 3. Correct alignment displayed oddly in IE 6/7.
 */

legend {
  border: 0; /* 1 */
  padding: 0;
  white-space: normal; /* 2 */
  *margin-left: -7px; /* 3 */
}

/**
 * 1. Correct font size not being inherited in all browsers.
 * 2. Address margins set differently in IE 6/7, Firefox 3+, Safari 5,
 *    and Chrome.
 * 3. Improve appearance and consistency in all browsers.
 */

button,
input,
select,
textarea {
  font-size: 100%; /* 1 */
  margin: 0; /* 2 */
  vertical-align: baseline; /* 3 */
  *vertical-align: middle; /* 3 */
}

/**
 * Address Firefox 3+ setting `line-height` on `input` using `!important` in
 * the UA stylesheet.
 */

button,
input {
  line-height: normal;
}

/**
 * Address inconsistent `text-transform` inheritance for `button` and `select`.
 * All other form control elements do not inherit `text-transform` values.
 * Correct `button` style inheritance in Chrome, Safari 5+, and IE 6+.
 * Correct `select` style inheritance in Firefox 4+ and Opera.
 */

button,
select {
  text-transform: none;
}

/**
 * 1. Avoid the WebKit bug in Android 4.0.* where (2) destroys native `audio`
 *    and `video` controls.
 * 2. Correct inability to style clickable `input` types in iOS.
 * 3. Improve usability and consistency of cursor style between image-type
 *    `input` and others.
 * 4. Remove inner spacing in IE 7 without affecting normal text inputs.
 *    Known issue: inner spacing remains in IE 6.
 */

button,
html input[type="button"], /* 1 */
input[type="reset"],
input[type="submit"] {
  -webkit-appearance: button; /* 2 */
  cursor: pointer; /* 3 */
  *overflow: visible;  /* 4 */
}

/**
 * Re-set default cursor for disabled elements.
 */

button[disabled],
html input[disabled] {
  cursor: default;
}

/**
 * 1. Address box sizing set to content-box in IE 8/9.
 * 2. Remove excess padding in IE 8/9.
 * 3. Remove excess padding in IE 7.
 *    Known issue: excess padding remains in IE 6.
 */

input[type="checkbox"],
input[type="radio"] {
  box-sizing: border-box; /* 1 */
  padding: 0; /* 2 */
  *height: 13px; /* 3 */
  *width: 13px; /* 3 */
}

/**
 * 1. Address `appearance` set to `searchfield` in Safari 5 and Chrome.
 * 2. Address `box-sizing` set to `border-box` in Safari 5 and Chrome
 *    (include `-moz` to future-proof).
 */

input[type="search"] {
  -webkit-appearance: textfield; /* 1 */
  -moz-box-sizing: content-box;
  -webkit-box-sizing: content-box; /* 2 */
  box-sizing: content-box;
}

/**
 * Remove inner padding and search cancel button in Safari 5 and Chrome
 * on OS X.
 */

input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}

/**
 * Remove inner padding and border in Firefox 3+.
 */

button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0;
}

/**
 * 1. Remove default vertical scrollbar in IE 6/7/8/9.
 * 2. Improve readability and alignment in all browsers.
 */

textarea {
  overflow: auto; /* 1 */
  vertical-align: top; /* 2 */
}

/**
 * Remove most spacing between table cells.
 */

table {
  border-collapse: collapse;
  border-spacing: 0;
}

html,
button,
input,
select,
textarea {
  color: #222;
}


::-moz-selection {
  background: #b3d4fc;
  text-shadow: none;
}

::selection {
  background: #b3d4fc;
  text-shadow: none;
}

img {
  vertical-align: middle;
}

fieldset {
  border: 0;
  margin: 0;
  padding: 0;
}

textarea {
  resize: vertical;
}

.chromeframe {
  margin: 0.2em 0;
  background: #ccc;
  color: #000;
  padding: 0.2em 0;
}

.wrapper{
  max-width: 1060px;
  margin: 0 auto;
}
@media (max-width: 1087px) {
  .wrapper {
    max-width: 970px;
  }
}

@media (max-width: 980px) {
  .wrapper {
    max-width: 750px;
  }
}

@media (max-width: 767.98px) {
  .wrapper {
    max-width: 650px;
  }
}

a, a:active, a:focus {
  text-decoration: none;
  outline: none;
}

.btn {
  display: inline-block;
  font-size: 1rem;
  padding: 0.5rem 1rem;
  background-color: $back-color-light;
  background-size: 178px;
  background-position-y: 101px;
  color: #faf3e6;
  //text-shadow: 1px 1px 4px #be972e;
  border: 2px solid $border-color-superlightgrey;
  border-radius: 5px;
  //box-shadow: inset 0 0 3px 1px $border-color-orange;
  //transition: all 0.1s ease-in-out;
  cursor: pointer;
  &:hover {
    background-color: #484848;
  }
  &:active {
    box-shadow: inset 0 0 8px 4px #181818;

  }
  &:disabled {
    color: #dcdcdc;
    border-top: 2px solid $danger-color;
    border-bottom: 2px solid $danger-color;
    background-color: rgba(253, 104, 104, 0.05);
    cursor: not-allowed;
    &:hover {
      background: rgba(57, 57, 57, 0.3);
    }
    &:active {
      box-shadow: 0 0 0 0 #dcdcdc;
    }
  }
}

::placeholder {
  color: $text-color-light;
}
input {
  font-family: 'Montserrat';
  font-size: 17px;
  padding: 5px 10px;
  background-color: $back-color-dark;
  border-radius: 3px;
  color: $text-color-light;
  color-scheme: dark;
  border-top: 2px solid $border-color-lightgrey;
  border-bottom: 2px solid $border-color-lightgrey;
  border-left: 2px solid $back-color-light;
  border-right: 2px solid $back-color-light;
  //border-radius: 10px;
}
input:focus {
  outline: none;
  border-top-color: rgba(255, 174, 0, 0.5);
  border-bottom-color: rgba(255, 174, 0, 0.5);
  border-left-color: 2px solid $back-color-dark;
  border-right-color: 2px solid $back-color-dark;
}
input:-webkit-autofill {
  -webkit-box-shadow: inset 0 0 0 50px $back-color-dark !important; /* Цвет фона */
  -webkit-text-fill-color: $text-color-light !important; /* цвет текста */
  color: $text-color-light !important; /* цвет текста */
}
.validation {
  margin-top: 10px;
  color: $danger-color;
}

select {
  width: 100%;
  font-size: 17px;
  padding: 10px 10px;
  background-color: $back-color-dark;
  border-radius: 3px;
  color: $text-color-light;
  border-top: 2px solid $border-color-lightgrey;
  border-bottom: 2px solid $border-color-lightgrey;
  border-left: 2px solid $back-color-light;
  border-right: 2px solid $back-color-light;
  -moz-appearance: none;
  -webkit-appearance: none;
  appearance: none;
}
select:not([multiple]) {
  padding-right: 1.2em;
  background-repeat: no-repeat;
  background-position: calc(100% - 0.75em) 0.5em;
  background-size: 1.5em auto;
  background-image: url("assets/images/arrow_down.png");
}
select::-ms-expand {
  display: none;
}
select:focus {
  outline: none;
  border-top-color: rgba(255, 174, 0, 0.5);
  border-bottom-color: rgba(255, 174, 0, 0.5);
  border-left-color: 2px solid $back-color-dark;
  border-right-color: 2px solid $back-color-dark;
}

.paige__container {
  margin-top: 220px;
}

.main-style-box {
  width: 100%;
  padding: 20px;
  margin-bottom: 60px;
  border: 2px solid $border-color-superlightgrey;
  border-radius: 5px;
  box-shadow: inset 0 0 20px 6px $back-color-dark;
  background: $back-color-light;
}

.create__input {
  width: 100%;
  font-size: 18px;
  padding: 10px;
  color: $text-color-light;
  &.ng-invalid.ng-touched {
    border-top: 2px solid $border-color-lightgrey;
    border-bottom: 2px solid $border-color-lightgrey;
    border-right: 2px solid $danger-color;
    border-left: 2px solid $danger-color;
    background-color: rgba(253, 104, 104, 0.05);
  }
  &.ng-valid.ng-touched {
    border-top: 2px solid $border-color-lightgrey;
    border-bottom: 2px solid $border-color-lightgrey;
    border-right: 2px solid $valid-color;
    border-left: 2px solid $valid-color;
    background-color: rgba(170, 255, 142, 0.05);
  }
}

.role__select {
  &.ng-invalid.ng-touched {
    border-top: 2px solid $border-color-lightgrey;
    border-bottom: 2px solid $border-color-lightgrey;
    border-right: 2px solid $danger-color;
    border-left: 2px solid $danger-color;
    background-color: rgba(253, 104, 104, 0.05);
  }
  &.ng-valid.ng-touched {
    border-top: 2px solid $border-color-lightgrey;
    border-bottom: 2px solid $border-color-lightgrey;
    border-right: 2px solid $valid-color;
    border-left: 2px solid $valid-color;
    background-color: rgba(170, 255, 142, 0.05);
  }
}
